<template>
  <div class="flex justify-center py-40">
    <div class="w-full max-w-sm">
      <div class="bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4">
        <div class="mb-4">
          <h1 class="text-3xl text-center">Payment</h1>
        </div>
        <div v-for="item in cartList" class="font-mono">
          <div>
            {{ item.name }}
          </div>
          <div class="flex justify-between">
            <div>
              @{{ item.price | money }}
              &times; {{ item.qty }}
            </div>
            <div>
              {{ item.qty * item.price | money }}
            </div>
          </div>
          <hr class="border-dotted">
        </div>

        <div class="mb-4">
          <div class="flex justify-between font-mono font-bold">
            <div>Total</div>
            <div>{{ totalAmount | money }}</div>
          </div>
        </div>

        <div class="flex items-center justify-center">
          <button @click="printBill" class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline" type="button">
            Print Bill
          </button>
        </div>
      </div>
      <div class="text-center">
        <button @click="cancel" class="text-gray-600 font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline" type="button">
          Cancel
        </button>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapState, mapGetters } from 'vuex'

  export default {
    name: 'Checkout',

    computed: {
      ...mapState(['cartList']),
      ...mapGetters(['totalAmount']),
    },

    methods: {
      cancel() {
        this.$router.push('/?category=')
      },

      printBill() {
        this.$store.dispatch('clearCartList')
        this.$router.push('/?category=')
      }
    }
  }
</script>
